import { React, useState } from "react";
import * as s from "../styles/globalStyles";
import dragon_1 from "../_resources/article/dragon_1.png";
import dragon_2 from "../_resources/article/dragon_2.png";
import probability from "../_resources/article/probability_table.png";

function Story() {
  return (
    <s.SubContainer>
      <s.TextTitle>
        Alpha Draconis NFT — A Collect To Earn Adventure Awaits
      </s.TextTitle>

      <figure>
        <s.ArticleImage src={dragon_1}></s.ArticleImage>
        <s.Caption>The hunt is on</s.Caption>
      </figure>

      <s.TextDescriptionNarrow>
        {" "}
        Inspired by the popular Anime Dragon Ball Z, our project revolves around
        a collecting game which rewards holders for holding our NFTs. Each Alpha
        Draconis NFT will be assigned a number from 1 to 7 which acts as the
        stars on the dragon ball. This number will be rerolled fortnightly and
        it will be available to check on our website and discord. At the end of
        the two weeks, a snapshot will be taken and those who managed to collect
        all 7 dragon balls will be eligible to share the pot of bounty provided
        by the team. This bounty will be generous and in the future will be
        funded by the royalties generated via the sales of the NFTs.
      </s.TextDescriptionNarrow>

      <s.SpacerMedium />

      <s.ArticleImage src={probability}></s.ArticleImage>
      <s.TextDescriptionNarrow>
        {" "}
        The probability of getting a 7 star ball is the lowest while the 1 star
        will be the highest. Hence during every cycle of the game there is less
        than a 1% chance your dragon will be getting a 7 star dragon ball. This
        can cause your NFT to be in demand all of a sudden. If you list your NFT
        to sell for cheap and it randomly got assigned a 7 star ball, you can be
        assured you NFT will be snapped up in a flash.
        <br></br>
        <br></br>
        This will incentivize people to delist their NFT or at least not list
        them for a low value. This will also create a cycle that can help
        sustain the game using the royalties and is a way of us giving back to
        reward the holders and believers of our project.
      </s.TextDescriptionNarrow>

      <s.SpacerMedium />

      <figure>
        <s.ArticleImage src={dragon_2}></s.ArticleImage>
        {/* <s.Caption>Legendary Undead Dragon</s.Caption> */}
      </figure>
      <s.TextSubTitle>Legendary Dragons</s.TextSubTitle>
      <s.TextDescriptionNarrow>
        The benefit of holding a legendary dragon is immense. It can be used as
        a wild card in our collecting game. For example if you hold the dragons
        with balls numbered 1 to 6 and is missing the last numbered ball, this
        legendary dragon can be used to filled up the void. If you somehow
        managed to own 7 legendary dragons, you will be guaranteed to split the
        bounty during every cycle of the game. Legendary dragons will be raffled
        off to minters.
        <br></br>
        <br></br>
        {/* We look forward to hearing your feedback. Wishing everyone a wonderful
        year ahead! */}
      </s.TextDescriptionNarrow>
    </s.SubContainer>
  );
}

export { Story };
